.chatWindow {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.chatWindow--header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chatWindow--headerinfo {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.chatWindow--avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-left: 15px;
    margin-right: 15px;
}

.chatWindow--name {
    font-size: 17px;
    color: var(--other-color5);
}

.chatWindow--headerbuttons {
    display: flex;
    align-items: center;
    margin-right: 15px;
}
.chatWindow--btn {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: hidden;
    transition: all ease 0.3s;
}

.chatWindow--body {
    flex: 1;
    overflow-y: auto;
    background-color: #E5DDD5;
    background-size: cover;
    background-position: top;
    background-image: var(--background);
    padding: 20px 30px;
}

.chatWindow--body::-webkit-scrollbar{
    width: 6px;
    height: 6px;
  }
  
  .chatWindow--body::-webkit-scrollbar-thumb{
    background-color: rgba(0, 0, 0, 0.2);
  }

.chatWindow--footer {
    height: 62px;
    display: flex;
    align-items: center;
}

.chatWindow--pre {
    display: flex;
    margin: 0 15px;
}

.chatWindow--inputarea {
    flex: 1;
}

.chatWindow--input {
    width: 100%;
    height: 40px;
    border: 0;
    outline: 0;
    background-color: var(--other-color3);
    border-radius: 20px;
    font-size: 15px;
    color: var(--other-color5);
    padding-left: 15px;
}

.chatWindow--pos {
    display: flex;
    margin: 0 15px;
}

.chatWindow--emojiarea {
    height: 200px;
    overflow-y: hidden;
    transition: all ease 0.3s;
}

aside.emoji-picker-react {
    width: auto;
    background: none;
    border: none;
}

.emoji-picker-react .emoji-group:before {
    background: none;
}
.gifpicker-container{
    position:inherit;
    width: 100% !important;
}
.chatWindow--gifarea {
    height: 200px;
    overflow-y: hidden;
    transition: all ease 0.3s;
}
